.about {
  text-align: center;
}

.hi {
  font-size: 40px;
  font-family: "Merriweather";
  font-weight: 700;
  color: #707070;
}

.name {
  margin-top: -10px;
  font-size: 60px;
  font-family: Lucida Sans Typewriter;
  color: #2f548f;
  line-height: 0;
}

.profile {
  margin-top: -20px;

  width: 30%;
}

.paragraph {
  width: 94%;
  font-family: "Lato";
  font-weight: 200;
  font-size: 22px;
  padding-left: 3%;
}


@media screen and (min-width: 995px) {
  .aboutContainer {

  }
  
  .hi {
