    font-family: Lucida Sans Typewriter;
    color: #2f548f;
    line-height: 1.2;
  
  }
  
  .profile{
    margin-top: -7%;
      width: 200px;
  }
  
  .paragraph {
    margin-top: -0.3%;
    width: 94.5%;
    font-family: "Lato";
    font-weight: 200;
    font-size: 20px;
  }


